"use client"

import { useEffect, useState } from "react"
import Image from "next/image"
import {
  Plus,
  Search,
  Edit2,
  Trash2,
  X,
  Upload,
  ChevronLeft,
  ChevronRight,
  LayoutGrid,
  List,
  GripVertical,
  Info,
} from "lucide-react"
import { useAuth } from "@/lib/auth-context"
import {
  DragDropContext,
  Droppable,
  Draggable,
  type DropResult,
} from "@hello-pangea/dnd"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog"
import { toast } from "sonner"

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105"

type ViewMode = "list" | "grid"
const VIEW_MODE_KEY = "adminProdutosViewMode"

// ===== Tipos auxiliares (vindo do backend) =====

type Status = "ativo" | "inativo"

interface CategoryOption {
  id: string // Guid em string
  nome: string
}

interface SizeOption {
  id: number
  nome: string
  ativo: boolean
}

interface ColorOption {
  id: number
  nome: string
  codigoHex: string
  ativo: boolean
}

interface ProductImageDto {
  id: string
  productId: string
  url: string
  altText?: string | null
  isPrimary: boolean
  displayOrder: number
  createdAt: string
}

interface ProductResponseDto {
  id: string
  categoryId: string
  name: string
  description?: string | null
  price: number
  promotionalPrice?: number | null
  sizeIds: number[]
  colorIds: number[]
  isPersonalizable: boolean
  isActive: boolean
  createdAt: string
  images: ProductImageDto[]
}

// Modelo usado internamente no front (nomes PT-BR)
interface Product {
  id: string
  nome: string
  preco: number
  precoPromocional: number | null
  categoriaId: string
  tamanhos: number[]
  cores: number[]
  status: Status
  personalizavel: boolean
  descricao: string
  imagens: ProductImageDto[]
  createdAt: string
}

interface ModalImage {
  id: string // GUID para existentes, temp ID para novos
  url: string
  file?: File
  isNew: boolean
}

export default function AdminProdutos() {
  const { token } = useAuth()

  const [products, setProducts] = useState<Product[]>([])
  const [categories, setCategories] = useState<CategoryOption[]>([])
  const [sizes, setSizes] = useState<SizeOption[]>([])
  const [colors, setColors] = useState<ColorOption[]>([])

  const [searchTerm, setSearchTerm] = useState("")
  const [filterCategoria, setFilterCategoria] = useState("")

  const [viewMode, setViewMode] = useState<ViewMode>(() => {
    if (typeof window === "undefined") return "list"
    const saved = window.localStorage.getItem(VIEW_MODE_KEY) as ViewMode | null
    return saved === "list" || saved === "grid" ? saved : "list"
  })

  const changeViewMode = (mode: ViewMode) => {
    setViewMode(mode)
    if (typeof window !== "undefined") {
      window.localStorage.setItem(VIEW_MODE_KEY, mode)
    }
  }

  const [showModal, setShowModal] = useState(false)
  const [editingProduct, setEditingProduct] = useState<Product | null>(null)

  const [formData, setFormData] = useState({
    nome: "",
    preco: "",
    precoPromocional: "",
    categoriaId: "",
    tamanhos: [] as number[],
    cores: [] as number[],
    descricao: "",
    status: "ativo" as Status,
    personalizavel: false,
  })

  // Estado unificado de imagens para o modal
  const [modalImages, setModalImages] = useState<ModalImage[]>([])

  // Estados para controlar AlertDialogs
  const [deleteImageDialog, setDeleteImageDialog] = useState<{
    open: boolean
    index: number | null
  }>({ open: false, index: null })

  const [deleteProductDialog, setDeleteProductDialog] = useState<{
    open: boolean
    productId: string | null
    productName: string
    isActive: boolean
  }>({ open: false, productId: null, productName: "", isActive: true })

  // ===== Helper de headers SEM undefined =====
  const buildAuthHeaders = (extra?: Record<string, string>): Record<string, string> => {
    const headers: Record<string, string> = { ...(extra ?? {}) }

    if (token) {
      headers["Authorization"] = `Bearer ${token}`
    }

    return headers
  }

  // ===== Carregar dados iniciais =====
  const loadData = async () => {
    try {
      const [catRes, sizeRes, colorRes, prodRes] = await Promise.all([
        fetch(`${API_BASE_URL}/api/admin/categories`, {
          headers: buildAuthHeaders(),
          cache: "no-store",
        }),
        fetch(`${API_BASE_URL}/api/admin/sizes`, {
          headers: buildAuthHeaders(),
          cache: "no-store",
        }),
        fetch(`${API_BASE_URL}/api/admin/colors`, {
          headers: buildAuthHeaders(),
          cache: "no-store",
        }),
        fetch(`${API_BASE_URL}/api/admin/products`, {
          headers: buildAuthHeaders(),
          cache: "no-store",
        }),
      ])

      if (!catRes.ok || !sizeRes.ok || !colorRes.ok || !prodRes.ok) {
        console.error(
          "Erro ao buscar dados de admin (categorias/tamanhos/cores/produtos).",
        )
        return
      }

      const catJson = await catRes.json()
      const sizeJson = await sizeRes.json()
      const colorJson = await colorRes.json()
      const prodJson: ProductResponseDto[] = await prodRes.json()

      const mappedCategories: CategoryOption[] = (catJson ?? []).map(
        (c: any) => ({
          id: c.id,
          nome: c.name as string,
        }),
      )

      const mappedSizes: SizeOption[] = (sizeJson ?? []).map((s: any) => ({
        id: s.id as number,
        nome: s.name as string,
        ativo: Boolean(s.isActive),
      }))

      const mappedColors: ColorOption[] = (colorJson ?? []).map((c: any) => ({
        id: c.id as number,
        nome: c.name as string,
        codigoHex: c.hexCode as string,
        ativo: Boolean(c.isActive),
      }))

      const mappedProducts: Product[] = (prodJson ?? []).map((p) => ({
        id: p.id,
        nome: p.name,
        preco: p.price,
        precoPromocional: p.promotionalPrice ?? null,
        categoriaId: p.categoryId,
        tamanhos: p.sizeIds ?? [],
        cores: p.colorIds ?? [],
        status: p.isActive ? "ativo" : "inativo",
        personalizavel: p.isPersonalizable,
        descricao: p.description ?? "",
        imagens: (p.images ?? [])
          .slice()
          .sort((a, b) => {
            // Garante que principal sempre vem primeiro
            if (a.isPrimary !== b.isPrimary) return a.isPrimary ? -1 : 1
            return a.displayOrder - b.displayOrder
          }),
        createdAt: p.createdAt,
      }))

      setCategories(mappedCategories)
      setSizes(mappedSizes)
      setColors(mappedColors)
      setProducts(mappedProducts)
    } catch (error) {
      console.error("Erro ao carregar dados de admin:", error)
    }
  }

  useEffect(() => {
    void loadData()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  // ===== Filtros =====
  const filteredProducts = products.filter((p) => {
    const matchSearch = p.nome.toLowerCase().includes(searchTerm.toLowerCase())
    const matchCategoria = !filterCategoria || p.categoriaId === filterCategoria
    return matchSearch && matchCategoria
  })

  // ===== Ordenação: ativos primeiro, inativos no final =====
  const sortedProducts = [...filteredProducts].sort((a, b) => {
    if (a.status === b.status) {
      // Dentro do mesmo status: ordenar pelos mais novos primeiro
      const dateA = new Date(a.createdAt).getTime()
      const dateB = new Date(b.createdAt).getTime()
      return dateB - dateA
    }

    // Ativos primeiro, inativos no final
    return a.status === "ativo" ? -1 : 1
  })

  // ===== Modal / Upload helpers =====
  const openCreateModal = () => {
    setEditingProduct(null)
    setFormData({
      nome: "",
      preco: "",
      precoPromocional: "",
      categoriaId: "",
      tamanhos: [],
      cores: [],
      descricao: "",
      status: "ativo",
      personalizavel: false,
    })
    setModalImages([])
    setShowModal(true)
  }

  const openEditModal = (product: Product) => {
    setEditingProduct(product)
    setFormData({
      nome: product.nome,
      preco: product.preco.toString(),
      precoPromocional: product.precoPromocional?.toString() ?? "",
      categoriaId: product.categoriaId,
      tamanhos: product.tamanhos ?? [],
      cores: product.cores ?? [],
      descricao: product.descricao,
      status: product.status,
      personalizavel: product.personalizavel ?? false,
    })

    // Carrega imagens existentes (ordem já vem organizada pelo loadData)
    setModalImages(
      product.imagens.map((img) => ({
        id: img.id,
        url: img.url,
        isNew: false,
      })),
    )
    setShowModal(true)
  }

  // ===== Gerenciamento de Imagens (Drag & Drop + Delete) =====

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files ?? [])
    if (files.length === 0) return

    const newImages: ModalImage[] = files.map((file) => ({
      id: `temp-${Date.now()}-${Math.random()}`, // ID temporário
      url: URL.createObjectURL(file),
      file: file,
      isNew: true,
    }))

    setModalImages((prev) => [...prev, ...newImages])
  }

  const handleDragEnd = (result: DropResult) => {
    if (!result.destination) return

    const items = Array.from(modalImages)
    const [reorderedItem] = items.splice(result.source.index, 1)
    items.splice(result.destination.index, 0, reorderedItem)

    setModalImages(items)
  }

  const handleRemoveImage = (index: number) => {
    const imageToRemove = modalImages[index]

    if (!imageToRemove.isNew) {
      // Imagem existente: abrir dialog de confirmação
      setDeleteImageDialog({ open: true, index })
    } else {
      // Imagem nova: remover diretamente sem confirmação
      const newImages = Array.from(modalImages)
      newImages.splice(index, 1)
      setModalImages(newImages)
    }
  }

  const confirmDeleteImage = async () => {
    const index = deleteImageDialog.index
    if (index === null) return

    const imageToRemove = modalImages[index]

    try {
      const res = await fetch(
        `${API_BASE_URL}/api/admin/products/images/${imageToRemove.id}`,
        {
          method: "DELETE",
          headers: buildAuthHeaders(),
        },
      )

      if (!res.ok) {
        console.error("Erro ao excluir imagem.")
        toast.error("Erro ao excluir imagem.")
        setDeleteImageDialog({ open: false, index: null })
        return
      }

      // Remove do estado
      const newImages = Array.from(modalImages)
      newImages.splice(index, 1)
      setModalImages(newImages)
      toast.success("Imagem excluída com sucesso!")
    } catch (error) {
      console.error("Erro ao excluir imagem:", error)
      toast.error("Erro ao conectar ao servidor")
    }

    setDeleteImageDialog({ open: false, index: null })
  }

  // ===== Salvar (create / update) =====
  const handleSave = async () => {
    try {
      const payload = {
        categoryId: formData.categoriaId,
        name: formData.nome,
        description: formData.descricao || null,
        price: Number.parseFloat(formData.preco || "0"),
        promotionalPrice: formData.precoPromocional
          ? Number.parseFloat(formData.precoPromocional)
          : null,
        sizeIds: formData.tamanhos,
        colorIds: formData.cores,
        isPersonalizable: formData.personalizavel,
        isActive: formData.status === "ativo",
      }

      let productId: string | null = null

      if (editingProduct) {
        // UPDATE
        const res = await fetch(
          `${API_BASE_URL}/api/admin/products/${editingProduct.id}`,
          {
            method: "PUT",
            headers: buildAuthHeaders({
              "Content-Type": "application/json",
            }),
            body: JSON.stringify(payload),
          },
        )

        if (!res.ok) {
          console.error("Erro ao atualizar produto.")
          return
        }

        productId = editingProduct.id
      } else {
        // CREATE
        const res = await fetch(`${API_BASE_URL}/api/admin/products`, {
          method: "POST",
          headers: buildAuthHeaders({
            "Content-Type": "application/json",
          }),
          body: JSON.stringify(payload),
        })

        if (!res.ok) {
          console.error("Erro ao criar produto.")
          return
        }

        const created: ProductResponseDto = await res.json()
        productId = created.id
      }

      // Processar Imagens (Upload de novas + Reordenação de existentes)
      if (productId) {
        const reorderList: {
          imageId: string
          displayOrder: number
          isPrimary: boolean
        }[] = []

        await Promise.all(
          modalImages.map(async (img, index) => {
            const isPrimary = index === 0

            if (img.isNew && img.file) {
              // Upload
              const fd = new FormData()
              fd.append("file", img.file)
              fd.append("isPrimary", isPrimary ? "true" : "false")
              fd.append("displayOrder", String(index))

              const url = `${API_BASE_URL}/api/admin/products/${productId}/images`

              try {
                const res = await fetch(url, {
                  method: "POST",
                  headers: buildAuthHeaders(),
                  body: fd,
                })
                if (!res.ok) {
                  console.error("Falha no upload da imagem", img.file.name)
                }
              } catch (e) {
                console.error("Erro no upload", e)
              }
            } else {
              // Existente -> Adicionar à lista de reordenação
              reorderList.push({
                imageId: img.id,
                displayOrder: index,
                isPrimary: isPrimary,
              })
            }
          }),
        )

        if (reorderList.length > 0) {
          try {
            await fetch(
              `${API_BASE_URL}/api/admin/products/${productId}/images/reorder`,
              {
                method: "POST",
                headers: buildAuthHeaders({
                  "Content-Type": "application/json",
                }),
                body: JSON.stringify(reorderList),
              },
            )
          } catch (e) {
            console.error("Erro ao reordenar imagens", e)
          }
        }
      }

      await loadData()

      setShowModal(false)
      setEditingProduct(null)
    } catch (error) {
      console.error("Erro ao salvar produto:", error)
    }
  }

  // ===== Delete com regra: 1º clique inativa, 2º clique tenta remover =====
  const handleDelete = (id: string) => {
    const target = products.find((p) => p.id === id)
    if (!target) return

    setDeleteProductDialog({
      open: true,
      productId: id,
      productName: target.nome,
      isActive: target.status === "ativo",
    })
  }

  const confirmDeleteProduct = async () => {
    const { productId, isActive } = deleteProductDialog
    if (!productId) return

    try {
      const res = await fetch(`${API_BASE_URL}/api/admin/products/${productId}`, {
        method: "DELETE",
        headers: buildAuthHeaders(),
      })

      if (!res.ok) {
        console.error("Erro ao excluir produto.")
        toast.error("Erro ao excluir produto.")
        setDeleteProductDialog({ open: false, productId: null, productName: "", isActive: true })
        return
      }

      // Atualiza estado local de acordo com o status atual
      if (isActive) {
        // 1º clique: apenas marca como inativo (não some da lista)
        setProducts((prev) =>
          prev.map((p) =>
            p.id === productId
              ? {
                  ...p,
                  status: "inativo" as Status,
                }
              : p,
          ),
        )
        toast.success("Produto marcado como inativo!")
      } else {
        // 2º clique: remove da lista (quando backend conseguir deletar de fato)
        setProducts((prev) => prev.filter((p) => p.id !== productId))
        toast.success("Produto excluído permanentemente!")
      }
    } catch (error) {
      console.error("Erro ao excluir produto:", error)
      toast.error("Erro ao conectar ao servidor")
    }

    setDeleteProductDialog({ open: false, productId: null, productName: "", isActive: true })
  }

  // ===== Render =====
  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Produtos</h1>
          <p className="text-foreground/60">
            Gerencie seu catálogo de produtos
          </p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Novo Produto
        </button>
      </div>

      {/* Alerta de Instruções de Exclusão */}
      <div className="mb-6 bg-blue-50 border border-blue-200 rounded-lg p-4 flex gap-3">
        <Info className="text-blue-600 flex-shrink-0 mt-0.5" size={20} />
        <div className="text-sm text-blue-900">
          <p className="font-medium mb-1">Como funciona a exclusão:</p>
          <ul className="list-disc list-inside space-y-1 text-blue-800">
            <li><strong>1º clique na lixeira:</strong> O produto fica inativo.</li>
            <li><strong>2º clique na lixeira:</strong> Se não houver vínculos a pedidos, o produto é removido permanentemente. Se houver vínculos, permanece inativo.</li>
          </ul>
        </div>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6 justify-between">
        <div className="flex flex-1 gap-4">
          <div className="relative flex-1">
            <Search
              size={18}
              className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50"
            />
            <input
              type="text"
              placeholder="Buscar por nome..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
            />
          </div>
          <select
            value={filterCategoria}
            onChange={(e) => setFilterCategoria(e.target.value)}
            className="px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          >
            <option value="">Todas as categorias</option>
            {categories.map((cat) => (
              <option key={cat.id} value={cat.id}>
                {cat.nome}
              </option>
            ))}
          </select>
        </div>

        <div className="flex items-center bg-muted rounded-lg p-1 border border-border">
          <button
            onClick={() => changeViewMode("list")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "list"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <List size={18} />
          </button>
          <button
            onClick={() => changeViewMode("grid")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "grid"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <LayoutGrid size={18} />
          </button>
        </div>
      </div>

      {/* Content */}
      {viewMode === "grid" ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {sortedProducts.map((product) => {
            const categoriaNome =
              categories.find((c) => c.id === product.categoriaId)?.nome ?? "-"
            const mainImage =
              product.imagens.length > 0
                ? product.imagens[0].url
                : "/placeholder.svg"

            const isInactive = product.status === "inativo"

            return (
              <div
                key={product.id}
                className={`bg-card border rounded-xl overflow-hidden transition-all ${
                  isInactive
                    ? "border-border/50 opacity-70"
                    : "border-border"
                }`}
              >
                <div className="aspect-square relative bg-muted">
                  <Image
                    src={mainImage}
                    alt={product.nome}
                    fill
                    className="object-cover"
                  />
                  <div className="absolute top-2 right-2 flex gap-1">
                    <button
                      onClick={() => openEditModal(product)}
                      className="p-2 bg-background/80 backdrop-blur-sm text-foreground/80 hover:text-primary rounded-lg transition-colors shadow-sm"
                    >
                      <Edit2 size={16} />
                    </button>
                    <button
                      onClick={() => handleDelete(product.id)}
                      className="p-2 bg-background/80 backdrop-blur-sm text-foreground/80 hover:text-red-600 rounded-lg transition-colors shadow-sm"
                    >
                      <Trash2 size={16} />
                    </button>
                  </div>
                  {product.precoPromocional && (
                    <div className="absolute top-2 left-2 px-2 py-1 bg-accent text-accent-foreground text-xs font-bold rounded-md shadow-sm">
                      PROMOÇÃO
                    </div>
                  )}
                </div>

                <div className="p-4">
                  <div className="flex items-start justify-between mb-2 gap-2">
                    <div>
                      <p className="text-xs text-foreground/60 mb-1">
                        {categoriaNome}
                      </p>
                      <h3 className="font-medium text-foreground line-clamp-2 leading-tight">
                        {product.nome}
                      </h3>
                    </div>
                  </div>

                  <div className="flex items-end justify-between mt-4">
                    <div>
                      {product.precoPromocional ? (
                        <div className="flex flex-col">
                          <span className="text-xs text-foreground/50 line-through">
                            R$ {product.preco.toFixed(2)}
                          </span>
                          <span className="font-bold text-accent">
                            R$ {product.precoPromocional.toFixed(2)}
                          </span>
                        </div>
                      ) : (
                        <span className="font-bold text-foreground">
                          R$ {product.preco.toFixed(2)}
                        </span>
                      )}
                    </div>
                    <span
                      className={`px-2 py-1 rounded-full text-xs font-medium ${
                        product.status === "ativo"
                          ? "bg-green-100 text-green-700"
                          : "bg-gray-100 text-gray-700"
                      }`}
                    >
                      {product.status === "ativo" ? "Ativo" : "Inativo"}
                    </span>
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      ) : (
        <div className="bg-card border border-border rounded-xl overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-muted border-b border-border">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Produto
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Preço
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Categoria
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Status
                  </th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-foreground">
                    Ações
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-border">
                {sortedProducts.map((product) => {
                  const categoriaNome =
                    categories.find((c) => c.id === product.categoriaId)?.nome ?? "-"
                  const mainImage =
                    product.imagens.length > 0 ? product.imagens[0].url : "/placeholder.svg"

                  const isInactive = product.status === "inativo"

                  return (
                    <tr
                      key={product.id}
                      className={`transition-colors ${
                        isInactive
                          ? "opacity-70 bg-muted/30"
                          : "hover:bg-muted/50"
                      }`}
                    >
                      <td className="px-6 py-4">
                        <div className="flex items-center gap-3">
                          <div className="w-12 h-12 rounded-lg bg-muted overflow-hidden">
                            <Image
                              src={mainImage}
                              alt={product.nome}
                              width={48}
                              height={48}
                              className="w-full h-full object-cover"
                            />
                          </div>
                          <span className="font-medium text-foreground">
                            {product.nome}
                          </span>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <div>
                          {product.precoPromocional ? (
                            <>
                              <span className="text-sm text-foreground/50 line-through">
                                R$ {product.preco.toFixed(2)}
                              </span>
                              <span className="ml-2 font-medium text-accent">
                                R$ {product.precoPromocional.toFixed(2)}
                              </span>
                            </>
                          ) : (
                            <span className="font-medium text-foreground">
                              R$ {product.preco.toFixed(2)}
                            </span>
                          )}
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm text-foreground/70">
                        {categoriaNome}
                      </td>
                      <td className="px-6 py-4">
                        <span
                          className={`px-2 py-1 rounded-full text-xs font-medium ${
                            product.status === "ativo"
                              ? "bg-green-100 text-green-700"
                              : "bg-red-100 text-red-700"
                          }`}
                        >
                          {product.status === "ativo" ? "Ativo" : "Inativo"}
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center justify-center gap-2">
                          <button
                            onClick={() => openEditModal(product)}
                            className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                          >
                            <Edit2 size={16} />
                          </button>
                          <button
                            onClick={() => handleDelete(product.id)}
                            className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  )
                })}
              </tbody>
            </table>
          </div>

          {/* Pagination (mock visual) */}
          <div className="flex items-center justify-between px-6 py-4 border-t border-border">
            <p className="text-sm text-foreground/60">
              Mostrando {filteredProducts.length} de {products.length} produtos
            </p>
            <div className="flex items-center gap-2">
              <button className="p-2 border border-border rounded-lg hover:bg-muted transition-colors">
                <ChevronLeft size={16} />
              </button>
              <button className="px-3 py-1 bg-primary text-primary-foreground rounded-lg text-sm">
                1
              </button>
              <button className="px-3 py-1 hover:bg-muted rounded-lg text-sm">
                2
              </button>
              <button className="p-2 border border-border rounded-lg hover:bg-muted transition-colors">
                <ChevronRight size={16} />
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingProduct ? "Editar Produto" : "Novo Produto"}
              </h2>
              <button
                onClick={() => setShowModal(false)}
                className="text-foreground/60 hover:text-foreground"
              >
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              {/* Image Upload with Drag and Drop */}
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Imagens
                </label>

                <label className="block border-2 border-dashed border-border rounded-lg p-8 text-center hover:border-accent transition-colors cursor-pointer mb-4">
                  <input
                    type="file"
                    multiple
                    accept="image/*"
                    className="hidden"
                    onChange={handleFileChange}
                  />
                  <Upload
                    size={32}
                    className="mx-auto text-foreground/40 mb-2"
                  />
                  <p className="text-sm text-foreground/60">
                    Clique ou arraste imagens aqui
                  </p>
                  <p className="text-xs text-foreground/40 mt-1">
                    PNG, JPG até 5MB
                  </p>
                </label>

                {modalImages.length > 0 && (
                  <DragDropContext onDragEnd={handleDragEnd}>
                    <Droppable droppableId="images" direction="horizontal">
                      {(provided) => (
                        <div
                          {...provided.droppableProps}
                          ref={provided.innerRef}
                          className="grid grid-cols-3 gap-3"
                        >
                          {modalImages.map((img, index) => (
                            <Draggable
                              key={img.id}
                              draggableId={img.id}
                              index={index}
                            >
                              {(provided) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  {...provided.dragHandleProps}
                                  className="relative w-full h-24 rounded-lg overflow-hidden bg-muted border border-border group"
                                >
                                  <Image
                                    src={img.url}
                                    alt={`Imagem ${index + 1}`}
                                    width={120}
                                    height={96}
                                    className="w-full h-full object-cover"
                                  />
                                  <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center gap-2">
                                    <GripVertical
                                      className="text-white cursor-move"
                                      size={20}
                                    />
                                    <button
                                      type="button"
                                      onClick={() => handleRemoveImage(index)}
                                      className="p-1 bg-red-500 text-white rounded hover:bg-red-600 transition-colors"
                                    >
                                      <X size={16} />
                                    </button>
                                  </div>
                                  {index === 0 && (
                                    <div className="absolute top-1 left-1 px-1.5 py-0.5 bg-primary text-primary-foreground text-[10px] font-bold rounded shadow-sm">
                                      Principal
                                    </div>
                                  )}
                                </div>
                              )}
                            </Draggable>
                          ))}
                          {provided.placeholder}
                        </div>
                      )}
                    </Droppable>
                  </DragDropContext>
                )}
              </div>

              <div className="grid grid-cols-1 gap-4">
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Nome do Produto
                  </label>
                  <input
                    type="text"
                    value={formData.nome}
                    onChange={(e) =>
                      setFormData({ ...formData, nome: e.target.value })
                    }
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Preço (R$)
                  </label>
                  <input
                    type="number"
                    step="0.01"
                    value={formData.preco}
                    onChange={(e) =>
                      setFormData({ ...formData, preco: e.target.value })
                    }
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Preço Promocional
                  </label>
                  <input
                    type="number"
                    step="0.01"
                    value={formData.precoPromocional}
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        precoPromocional: e.target.value,
                      })
                    }
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                    placeholder="Opcional"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Categoria
                  </label>
                  <select
                    value={formData.categoriaId}
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        categoriaId: e.target.value,
                      })
                    }
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  >
                    <option value="">Selecione...</option>
                    {categories.map((cat) => (
                      <option key={cat.id} value={cat.id}>
                        {cat.nome}
                      </option>
                    ))}
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Status
                  </label>
                  <select
                    value={formData.status}
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        status: e.target.value as Status,
                      })
                    }
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  >
                    <option value="ativo">Ativo</option>
                    <option value="inativo">Inativo</option>
                  </select>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Tamanhos
                </label>
                <div className="flex flex-wrap gap-2 p-2 border border-border rounded-lg bg-background min-h-[42px]">
                  {sizes.map((size) => (
                    <label
                      key={size.id}
                      className={`flex items-center gap-1 px-2 py-1 rounded cursor-pointer text-sm border transition-colors ${
                        formData.tamanhos.includes(size.id)
                          ? "bg-primary/10 border-primary text-primary"
                          : "bg-muted border-transparent text-foreground/70 hover:bg-muted/80"
                      }`}
                    >
                      <input
                        type="checkbox"
                        className="hidden"
                        checked={formData.tamanhos.includes(size.id)}
                        onChange={(e) => {
                          const newSizes = e.target.checked
                            ? [...formData.tamanhos, size.id]
                            : formData.tamanhos.filter((id) => id !== size.id)
                          setFormData({ ...formData, tamanhos: newSizes })
                        }}
                      />
                      {size.nome}
                    </label>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Cores
                </label>
                <div className="flex flex-wrap gap-2 p-2 border border-border rounded-lg bg-background min-h-[42px]">
                  {colors
                    .filter((c) => c.ativo)
                    .map((color) => (
                      <label
                        key={color.id}
                        className={`flex items-center gap-2 px-3 py-2 rounded cursor-pointer text-sm border transition-colors ${
                          formData.cores.includes(color.id)
                            ? "bg-primary/10 border-primary text-primary"
                            : "bg-muted border-transparent text-foreground/70 hover:bg-muted/80"
                        }`}
                      >
                        <input
                          type="checkbox"
                          className="hidden"
                          checked={formData.cores.includes(color.id)}
                          onChange={(e) => {
                            const newColors = e.target.checked
                              ? [...formData.cores, color.id]
                              : formData.cores.filter((id) => id !== color.id)
                            setFormData({
                              ...formData,
                              cores: newColors,
                            })
                          }}
                        />
                        <div
                          className="w-4 h-4 rounded-full border border-border"
                          style={{ backgroundColor: color.codigoHex }}
                        />
                        {color.nome}
                      </label>
                    ))}
                </div>
              </div>

              <div className="flex items-center gap-2 pt-4">
                <input
                  type="checkbox"
                  id="personalizavel"
                  checked={formData.personalizavel}
                  onChange={(e) =>
                    setFormData({
                      ...formData,
                      personalizavel: e.target.checked,
                    })
                  }
                  className="w-4 h-4 rounded border-border text-primary focus:ring-primary"
                />
                <label
                  htmlFor="personalizavel"
                  className="text-sm font-medium text-foreground cursor-pointer"
                >
                  Produto Personalizável
                </label>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Descrição
                </label>
                <textarea
                  rows={3}
                  value={formData.descricao}
                  onChange={(e) =>
                    setFormData({ ...formData, descricao: e.target.value })
                  }
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent resize-none"
                />
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
              >
                {editingProduct ? "Salvar Alterações" : "Criar Produto"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}